'use strict';

import $ from 'jquery';

export default class Button {
  constructor() {
    let button = $('.button');

    button.on('mousemove', function(event) {
      // console.log(event);

      let x = event.pageX - $(this).offset().left,
      y = event.pageY - $(this).offset().top,
      glow = $(this).find('.glow');

      glow.css({
        'left': `${x}px`,
        'top': `${y}px`
      });
    });

  }
}
