'use strict';

import $ from 'jquery';

export default class SearchBox {
  constructor() {

    let m_search_bar = $(".m-search-bar"),
    search_trigger = $('.header__search'),
    desktop_search = $('.d-search-bar'),
    non_search_components = $('.non-search'),
    main_menu = $('.main-menu'),
    mobile_menu_icon_container = $('.mobile-menu-icon-container'),
    search_text_placeholder = $('.search-results__title').find('span'),
    d_search_input = desktop_search.find('.siteSearch'),
    d_search_btn = desktop_search.find('.btn-search'),
    m_search_btn = m_search_bar.find('.btn-search'),
    m_search_input = m_search_bar.find('.mobileSearch');

    $(window).load(function() {

      let query_string = $.urlParam('q');

      if (query_string) {
        search_text_placeholder.text(` ${query_string}`);
      }

    });


    // clicking on mobile search button
    m_search_btn.click(function() {
      run_search(m_search_input);
    });

    // clicking on desktop search button
    d_search_btn.click(function() {
      run_search(d_search_input);
    });

    // pressing enter button while in search input
    d_search_input.bind("enter",function(e){
      run_search($(this));
    });

    d_search_input.keyup(function(e){
      if(e.keyCode == 13)
      {
        $(this).trigger("enter");
      }
    });

    search_trigger.click(function() {
      $(this).toggleClass('active');
      desktop_search.toggleClass('expand');
      main_menu.removeClass('visible');
      non_search_components.toggleClass('hide');
      mobile_menu_icon_container.removeClass('is-active');
    });

    // tapping outside of desktop search
    $(document).mouseup(function(e) {
      var container = $('.d-search-bar');
      // if the target of the click isn't the container nor a descendant of the container

      if (!$(e.target).hasClass('icon-search')) {
        if (!container.is(e.target) && container.has(e.target).length === 0)
        {
          search_trigger.removeClass('active');
          non_search_components.removeClass('hide');
          container.removeClass('expand');
        }
      }
    });

    // tapping outside of mobile search
    $(document).mouseup(function(e) {
      var container = $('.m-search-bar');

      // if the target of the click isn't the container nor a descendant of the container
      if (!$(e.target).hasClass('icon-search')) {
        if (!container.is(e.target) && container.has(e.target).length === 0)
        {
          container.removeClass('visible');
        }
      }
    });

    // helper functions
    function run_search(input) {
      let search_value = input.val(),
        search_param = input.attr('name') || 'q',
        current_url = window.location.origin;

      if (search_value) {
        window.location.href = `${current_url}/search/?${search_param}=${search_value}`;
      }
    }

    $.urlParam = function(name) {
      var results = new RegExp('[\?&]' + name + '=([^&#]*)').exec(window.location.href);
      if (results==null){
        return null;
      }
      else{
        return decodeURI(results[1]) || 0;
      }
    };

    window.emitter.on('showMobileSearch', function(event) {
      if(event == 'show') {
        m_search_bar.addClass('visible');
      } else {
        m_search_bar.removeClass('visible');
      }
      non_search_components.removeClass('hide');
    });
  }
}
