'use strict';

import $ from 'jquery';
import enquire from 'enquire.js';
import {bp} from '../../../_scripts/_helper';
import 'slick-carousel';
import ScrollMagic from 'scrollmagic';
// import 'scrollmagic-debug';

export default class Notification {
  constructor() {
    if($('.notification').length) {
      let $notifyBtn =  $('.js-notify-btn'),
          $notificationSection = $('.notification-section'),
          $notification = $('.notification', $notificationSection),
          $carousel = $('.notification__carousel', $notification),
          $backtotop = $('.back-to-top'),
          // $numbers = $('.numbers', $notifyBtn),
          notificationVisible = true; // By default set the notification visible

      // notification carousel
      function slickSettings() {
        return {
          infinite: false,
          speed: 1000,
          autoplaySpeed: 5000,
          autoplay: true,
          fade: true,
          slidesToShow: 1,
          slidesToScroll: 1,
          dots: true,
          dotsClass: 'custom_paging',
          customPaging: function (slider, i) {
              //FYI just have a look at the object to find available information
              //press f12 to access the console in most browsers
              //you could also debug or look in the source
              return  (i + 1) + '/' + slider.slideCount;
          },
          nextArrow: '<button class="notification-next notification-arrow"><i class="icon icon-chevron-small-right"></i><span class="visuallyhidden">Button to view the next items of the slideshow</span></button>',
          prevArrow: '<button class="notification-prev notification-arrow"><i class="icon icon-chevron-small-left"></i><span class="visuallyhidden">Button to view the previous items of the slideshow</span></button>'
        }
      }
      $carousel.slick(slickSettings());


      // Mobile notification
      // enquire.register("screen and (max-width:"+bp.minDesktop+"px)", {
      //   match : function() {
      //
      //     // Init ScrollMagic
      //     var controller = new ScrollMagic.Controller();
      //
      //     // build a scene
      //     var scene1 = new ScrollMagic.Scene({
      //       triggerElement: '#mainContent',
      //       duration: '70%'
      //     })
      //     .setClassToggle('.notification-btn', 'no-back-btn') // add class to project01
      //     .addTo(controller);
      //
      //
      //     $notifyBtn.on('click.mblNotification', function(e) {
      //       e.preventDefault();
      //       let $this = $(this);
      //       $notification.toggleClass('mbl-hidden');
      //       $this.toggleClass('active');
      //       // if($this.hasClass('active') && !$numbers.hasClass('viewed')) {
      //       //   $numbers.addClass('viewed');
      //       // }
      //       $backtotop.toggleClass('notify-open');
      //     });
      //   },
      //   unmatch : function() {
      //     $notifyBtn.off('click.mblNotification');
      //   }
      // });

      // Desktop notification
      enquire.register("screen and (min-width:"+bp.minDesktop+"px)", {
        match : function() {
          $notifyBtn.on('click.dskNotification', function(event) {
            event.preventDefault();

            $notificationSection.toggleClass('active');
            $notifyBtn.toggleClass('active');
          });
        },
        unmatch : function() {
          $notifyBtn.off('click.dskNotification');
          $notification.css('display',''); // reset display property
        }
      });


      // Use localStorage to check whether the notification is new or old
      if(typeof(Storage) !== "undefined") {

        let pubDate = $('.notification').attr('data-pubdate'), // get the notification published date
          notificationPublished = new Date(pubDate),
          notificationPublishedMs = notificationPublished.getTime(); // notification pusblished time in milliseconds

        if(localStorage.getItem("notifications") === null) { // This will be true if the user is new to the site or the user cleared cache
          localStorage.notifications = notificationPublishedMs; // set local storage value
        } else { // the below code will run if repeated user
          // compage existing localStorage value with notification published time
          if (localStorage.notifications != notificationPublishedMs) { // if the notification published time is different from localStorage
            notificationVisible = true;
            localStorage.notifications = notificationPublishedMs; // override the localStorage value
          } else {
            notificationVisible = false;
          }
        }
      }

      // show hide notification based on localStorage value
      if(notificationVisible) {
        enquire.register("screen and (min-width:"+bp.minDesktop+"px)", {
          match: function() {
            // if the notification is new, then open it on desktop by default
            if($('.home').length > 0) {
              $notifyBtn.addClass('active');
              $notificationSection.addClass('active');
            }
          }
        });
      }

    }

  }
}
