'use strict';

import $ from 'jquery';
import jQueryBridget from 'jquery-bridget';
import Masonry from 'masonry-layout';
import enquire from 'enquire.js';
import ScrollMagic from 'scrollmagic';
import imagesLoaded from 'imagesloaded';

export default class InstallationItem {
  constructor() {

    let installations_page = $('.installations-page'),
    installation_items_container = $('#installation-items'),
    installations_listing = $('.installations-listing'),
    get_installations = installations_page.data('endpoint'),
    dataParams = {
      itemsRequested: 6,
      nextStart: 1
    },
    first_load = true;

    if (installations_listing.is(':visible')) {
      add_items(dataParams, first_load);

      let controller = new ScrollMagic.Controller();

      let scene1 = new ScrollMagic.Scene({
        triggerElement: '.footer',
        triggerHook: 'onEnter'
      }).addTo(controller).on("enter", function (e) {
        dataParams.nextStart += 6;
        add_items(dataParams);
      });
    };

    function add_items(data_params, first_load) {
      $.ajax({
        url: get_installations,
        method: 'GET',
        dataType: 'json',
        data: data_params,
        ifModified: true
      }).success(function(data) {
        if (data != undefined) {
          process_data(data);
        }
      }).error(function(error) {
        console.log(error);
      });
    };

    // bridge Masonry to jQuery
    jQueryBridget('masonry', Masonry, $);


    enquire.register('screen and (max-width: 767px)', {
      match : function() {
        // remove masonry on handheld
        installation_items_container.masonry('destroy');
      }
    });

    // helper functions
    function process_data(data) {
      let items = data.items,
      installations_html = '';

      items.forEach(function(each, idx) {

        let html = `
        <a class="installations-listing__each ${each.width}" href="${each.url}">
        <div class="installations-listing__image" style="background-image: url(${each.image})">
        <img src="${each.image}" class="installations-listing__img">
        <div class="installations-listing__overlay">
        <p class="shine">FIND OUT MORE</p>
        </div>
        </div>
        <div class="installations-listing__content">
        <p class="title">${each.name}</p>
        <span class="shooting-star"></span>
        <p class="artists">${each.artists}</p>
        </div>
        </a>`;

        installations_html += html;
      });

      if (first_load) {
        installation_items_container.append(installations_html);

        installation_items_container.imagesLoaded(function() {
          installation_items_container.masonry({
            itemSelector: '.installations-listing__each',
            percentPosition: true,
            transitionDuration: '.3s',
            stagger: 30
          });
        })

        first_load = false;

      } else {
        let jquery_installations = $(installations_html);
        installation_items_container.append(jquery_installations).masonry('appended', jquery_installations);
      }
    };
  }
}
