'use strict';

let bp = {
  "maxMobile": 480,
  "Tablet": 768,
  "minDesktop": 1024
}

let debounce = function(func, wait, immediate) {
  var timeout;
  return function() {
    var context = this, args = arguments;
    var later = function() {
      timeout = null;
      if (!immediate) func.apply(context, args);
    };
    var callNow = immediate && !timeout;
    clearTimeout(timeout);
    timeout = setTimeout(later, wait);
    if (callNow) func.apply(context, args);
  };
}


let animateFigure = function animateFigure(o) {

        var interval = 50,
            timeLength = 1000,
            intervalId,
            num = parseFloat(o.data('figure')),
            o = o.find('.figure');

        var step = num / (timeLength / interval);

        intervalId = setInterval(function(){
            var curr = parseFloat(o.text());

            // curr = Math.round(curr * 100);

            curr += step;

            // curr = curr/100;

            curr = Math.round(curr);

            o.text(curr);

            if(parseFloat(curr+step) >= num) {
                o.text(num);
                clearInterval(intervalId);
            }

        }, interval);
    }

function switch_class(element, initial_class, new_class) {
  element.removeClass(initial_class).addClass(new_class);
};

export { bp, debounce, animateFigure, switch_class };
