'use strict';

import $ from 'jquery';
export default class MobileSearchBtn {
  constructor() {
    let $searchBtn = $('.js-search-menu');

    $searchBtn.on('click', function(event) {
      event.stopPropagation();
      $(this).toggleClass('is-active');
      if($(this).hasClass('is-active')) {
        window.emitter.emit('mobileMenu', 'hide'); // this emitter function is defined in hamburger-menu-btn atom
        window.emitter.emit('mobileSearch', 'show'); // this emitter function is defined below
      } else {
        window.emitter.emit('mobileSearch', 'hide'); // this emitter function is defined below
      }
    });

    // emitter function for mobile search
    window.emitter.on('mobileSearch', function(event) {
      if(event == 'show') {
        window.emitter.emit('showMobileSearch', 'show'); // this emitter function is defined in search-box atom
      } else {
        $searchBtn.removeClass('is-active');
        window.emitter.emit('showMobileSearch', 'hide'); // this emitter function is defined in search-box atom
      }
    });
  }
}
