'use strict';

import $ from 'jquery';

export default class FontResizer {
  constructor() {
    /* =========================================================
    >> Font resizer
    ============================================================ */
    let $fontResizer = $('.js-resizer');
    //change font size
    $fontResizer.on('click', function(e) {
      e.preventDefault();
      let size = $(this).data('size');

      $fontResizer.removeClass('is-active');
      $(this).addClass('is-active');


      $('#main-content').removeClass('font-normal font-large font-larger').addClass(`font-${size}`);

      // reset matchheight elements after font resize
      $.fn.matchHeight._update()

    });
  }
}
