'use strict';

import $ from 'jquery';
import matchHeight from 'jquery-match-height';

export default class SiteFooter {
  constructor() {

    let footer_height = $('footer').outerHeight(),
    sidebar_wrap = $('.sidebar__wrap'),
    window_height = $(window).outerHeight(),
    sidebar_top_offset = sidebar_wrap.css('top').replace('px', '');


    $('.footer__sponsor').matchHeight();

    $(window).on('load', function() {
      handle_sidebar();
    });

    $(document).scroll(function() {
      handle_sidebar();
    })

    function handle_sidebar() {
      let distance_from_bottom = Math.floor($(document).height() - $(document).scrollTop() - $(window).height()),
      sidebar_height = sidebar_wrap.css('height').replace('px', ''),
      sidebar_bottom_offset = window_height - parseInt(sidebar_top_offset) - parseInt(sidebar_height);

      if(distance_from_bottom + sidebar_bottom_offset <= footer_height) {
        sidebar_wrap.addClass('stick');
      } else {
        sidebar_wrap.removeClass('stick');
      }
    }


  }
}
