'use strict';

import $ from 'jquery';
import enquire from 'enquire.js';
import {bp} from '../../../_scripts/_helper';

export default class SiteHeader {
  constructor() {

    /* Mobile Menu */
    let menu_wrapper = $('.menu-wrapper'),
        mobilemenu_icon_container = $('.mobile-menu-icon-container'),
        search_btn = $('.js-search-menu'),
        mobile_search_bar = $('.m-search-bar'),
        mobile_search_icon = $('.mobile-search-icon');

    mobile_search_icon.click(function() {
      mobile_search_bar.toggleClass('visible');
    });

    let $mobileMenu = function(event) {
      event.stopPropagation();

      // remove the search if it is open
      if(search_btn.hasClass('is-active')) {
        search_btn.removeClass('is-active');
        mobile_search_bar.removeClass('search-open');
      }
    };

    mobilemenu_icon_container.on('click', $mobileMenu);

    // Reset Mobile Menu for desktop
    enquire.register("screen and (min-width:"+bp.minDesktop+"px)", {
      match : function() {
        mobilemenu_icon_container.removeClass('is-active');
        menu_wrapper.css('display', '');
      }
    });

    /* =========================================================
    >> Mobile Search
    ============================================================ */
    // search_btn.on('click', function(e) {

    //   console.log('clicked')
    //   e.stopPropagation();
    //   // close the menu if it is open
    //   if (mobilemenu_icon_container.hasClass('is-active')) {
    //     $mobileMenu(e);
    //     // $mobileMenuIcon.removeClass('open');
    //     mobilemenu_icon_container.removeClass('is-active'); // remove the active class from menu wrapper when clicking on search icon
    //     menu_wrapper.slideUp(150); // close the menu when clicking on search icon
    //   }


    //   $(this).toggleClass('is-active');
    //   mobile_search_bar.toggleClass('search-open');
    //   mobile_search_bar.find('.mobileSearch').focus();

    //   // Close search by clicking anywhere of the document
    //   $(document).on('click.closeSearchMenu', function (e) {
    //     if (!$(e.target).closest('.m-search-bar').length && !$(e.target).closest('.js-search-menu').length) {
    //       search_btn.removeClass('is-active');
    //       mobile_search_bar.removeClass('search-open');
    //     }
    //   });
    // });

    /*=============================================================
    >> Sticky kit only on desktop
    =============================================================== */
    enquire.register("screen and (min-width:"+bp.minDesktop+"px)", {
      match : function() {
        // sidebar sitcky
        $('.menu-notification').stick_in_parent({
          offset_top : 0,
          recalc_every: 1
        });
      },
      unmatch : function() {
        $('.menu-notification').trigger("sticky_kit:detach");
      }
    });

  }
}
