'use strict';

import '../../../_scripts/jquery-global';
import '../../../_scripts/jquery.easing.1.3';
import '../../../_scripts/jquery.parallax-scroll';
import enquire from 'enquire.js';
import Vivus from 'vivus';

export default class HomeBanner {
  constructor() {

    let scroll_to_illum = $('.canvas__scrollto').find('a'),
    letters_container = $('.lightup'),
    animation_letters = letters_container.find('h2'),
    canvas_delayappear = $('.canvas__delayappear'),
    bridges_of_time = $('#bridges_of_time'),
    canvas_scrollto = $('.canvas__scrollto'),
    silhouette = $('.silhouette'),
    galaxy = $('.galaxy1'),
    gradient = $('.gradient'),
    limit = 270;

    enquire.register("screen and (max-width: 1024px)", {
      match : function() {
        // when on handheld devices,
        setTimeout(function() {
          // 1) run automatic animation
          animate_on_mobile();
        }, 10);
      }
    });

    $(window).scroll(function() {
      if ($(window).width() > 960) {
        final_alignment();
        handle_opacity();
      }
    });

    $(window).load(function() {
      final_alignment();
    })

    scroll_to_illum.click(function(event) {
      event.preventDefault();
      // $('html, body').animate({
      //   scrollTop: $('.installations-section').offset().top
      // }, 800);
    });

    // helper functions
    function handle_opacity() {
      let st = $(window).scrollTop();

      /* avoid unnecessary call to jQuery function */
      if (st <= limit) {
        silhouette.css({ 'opacity' : (st/limit) });
        galaxy.css({ 'opacity' : (.3 - st/(2.5 * limit)) });
        gradient.css({ 'opacity' : (.3 - st/(2.5 * limit)) });
      }
    }

    function final_alignment() {
      if ($(window).scrollTop() > 200) {
        // show entire automated animation container
        canvas_delayappear.addClass('show');
        // handwriting
        animate_handwriting();
        canvas_scrollto.addClass('hide');
        // remove parallax scroll
        letters_container.removeAttr("data-parallax");

        // add shine
        animation_letters.removeAttr("data-parallax").addClass('shine');
        $(window).off('scroll');

        gradient.css('opacity', 0);
        galaxy.css('opacity', 0);
        silhouette.css('opacity', 1);
      }
    }

    function animate_handwriting() {

      bridges_of_time.addClass('show');

      if (bridges_of_time.is(':visible')) {
        return new Vivus('bridges_of_time', {
          duration: 170,
          type: 'oneByOne'
        });
      }
    };

    function animate_on_mobile() {
      animation_letters.animate({
        top: 0
      }, 1500, function() {
        $(this).addClass('shine static');
        canvas_delayappear.addClass('show');
        animate_handwriting();
      });

      silhouette.animate({
        opacity: 1
      }, 1500);

      galaxy.animate({
        opacity: 0
      }, 1500);
    };

  }
}
