// Main javascript entry point
// Should handle bootstrapping/starting application

'use strict';

import $ from 'jquery';
import 'fancybox';
import objectFitImages from 'object-fit-images';
import Emitter from 'tiny-emitter';
import Stickyfill from 'stickyfilljs';


import Rte from '../_modules/atoms/rte/rte';
import SearchBox from '../_modules/atoms/search-box/search-box';
import FontResizer from '../_modules/atoms/font-resizer/font-resizer';
import HamburgerMenuBtn from '../_modules/atoms/hamburger-menu-btn/hamburger-menu-btn';
import MobileSearchBtn from '../_modules/atoms/mobile-search-btn/mobile-search-btn';
import Notification from '../_modules/atoms/notification/notification';
import BackToTop from '../_modules/atoms/back-to-top/back-to-top';
import InstallationItem from '../_modules/atoms/installation-item/installation-item';
import Button from '../_modules/atoms/button/button';

import PrimaryMenu from '../_modules/molecules/primary-menu/primary-menu';
import Sidebar from '../_modules/molecules/sidebar/sidebar';
import Map from '../_modules/molecules/map/map';

import SiteHeader from '../_modules/organisms/site-header/site-header';
import HomeBanner from '../_modules/organisms/home-banner/home-banner';
import WhatsComing from '../_modules/organisms/whats-coming/whats-coming';
import VideoSection from '../_modules/organisms/video-section/video-section';
import InstallationsSection from '../_modules/organisms/installations-section/installations-section';
import ProgrammesSection from '../_modules/organisms/programmes-section/programmes-section';
import DetailBody from '../_modules/organisms/detail-body/detail-body';
import MediaListing from '../_modules/organisms/media-listing/media-listing';
import SiteFooter from '../_modules/organisms/site-footer/site-footer';

$(() => {
	window.emitter = new Emitter();

	if ($('#map').length) {
		new Map();
	}

	// polyfill for sticky in IE
	let sticky_elements = $('.sticky');
	Stickyfill.add(sticky_elements);

	new SiteFooter();
	new MediaListing();
	new Button();
	new DetailBody();
	new ProgrammesSection();
	new InstallationItem();
	new InstallationsSection();
	new VideoSection();
	new WhatsComing();

	new HomeBanner();
	new HamburgerMenuBtn();
	new MobileSearchBtn();
	new Notification();
	// new Accordion();
	new PrimaryMenu();
	new SiteHeader();
	new SearchBox();
	new FontResizer();
	// new HomeBanner();

	new Rte();

	// new Sidebar();

	new BackToTop();
	new objectFitImages(); // activate object fit


	/* fancybox initialize */
	$('.fancybox').fancybox({
		idleTime: false,
		loop: true
	});

	/* Jquery function to run smooth scrolling for anchor links like #goto.html */
	$('a[href*="#"]:not([href="#"])').click(function(e) {
		e.preventDefault();
		if (
			location.pathname.replace(/^\//, '') == this.pathname.replace(/^\//, '') ||
			location.hostname == this.hostname
		) {
			var target = $(this.hash);
			target = target.length ? target : $('[name=' + this.hash.slice(1) + ']');
			if (target.length) {
				$('html,body').animate(
					{
						scrollTop: target.offset().top
					},
					1000
				);
				return false;
			}
		}
	});

	// prevent jumping to top on clicking anchor tags everywhere
	$('a[href="#"]').click(function(e) {
		e.preventDefault();
	});

});
