'use strict';

import $ from 'jquery';
import 'slick-carousel';
import '@fancyapps/fancybox';

export default class DetailBody {
  constructor() {
    let detail_gallery = $('.detail-content__slick'),
      detail_fancyboxes = $('.detail-content__play, .detail-content__slick-video');

    detail_gallery.slick({
      dots: true,
      arrows: true,
      prevArrow:"<span class='icon icon-arrow_small_prev slick-arrow slick-prev'></span>",
      nextArrow:"<span class='icon icon-arrow_small_next slick-arrow slick-next'></span>",
      cssEase: 'linear'
    });

    detail_fancyboxes.fancybox();

    $('.detail-content__dp-wrap').slick({
      dots: true,
      arrows: true,
      prevArrow:"<span class='icon icon-arrow_small_prev slick-arrow slick-prev'></span>",
      nextArrow:"<span class='icon icon-arrow_small_next slick-arrow slick-next'></span>",
      cssEase: 'linear'
    });
  }
}
