'use strict';

import $ from 'jquery';
export default class HamburgerMenuBtn {
  constructor() {
    let $mobileMenuIcon = $('.mobile-menu-icon-container'),
      header_search_container = $('.header__search');

    $mobileMenuIcon.on('click', function(event) {
      event.stopPropagation();

      header_search_container.removeClass('active');

      $(this).toggleClass('is-active');

      if($(this).hasClass('is-active')) {
        window.emitter.emit('mobileSearch', 'hide'); // this emitter function is defined in mobile-search-btn atom
        window.emitter.emit('mobileMenu', 'show'); // this emitter function is defined below
      } else {
        window.emitter.emit('mobileMenu', 'hide'); // this emitter function is defined below
      }
    });

    // Emitter funciton for mobile menu
    window.emitter.on('mobileMenu', function(event) {
      if(event == 'show') {
        window.emitter.emit('showMobileMenu', 'show'); // this emitter function is defined in primary-menu molecule
      } else {
        $mobileMenuIcon.removeClass('is-active');
        window.emitter.emit('showMobileMenu', 'hide'); // this emitter function is defined in primary-menu molecule
      }
    });
  }
}
