'use strict';

import $ from 'jquery';
import enquire from 'enquire.js';
import {bp} from '../../../_scripts/_helper';

export default class PrimaryMenu {
  constructor() {

    let $nav = $(".main-menu");

    window.emitter.on('showMobileMenu', function(event) {
      if(event == 'show') {
        $nav.addClass('visible');
        console.log('visible')
      } else {
        $nav.removeClass('visible');
        console.log('Invisible')
      }
    });

    let $nav_item = $nav.find("li"),
        $dropdown = $nav_item.has("ul");


    enquire.register("screen and (max-width:"+bp.minDesktop+"px)", {
      match : function() {
        // let $back_btn = $nav_item.find("ul").prepend("<li class='js-back'><span><i class='icon icon-chevron-small-left'></i>back</span></li>");

        // open sub-level
        $dropdown.on("click.mobileNav", '.plus-minus-toggle', function(e) {
          e.preventDefault();
          e.stopPropagation();
          let $listItem = $(this).closest('li');
          $listItem.toggleClass("open");
          $(this).toggleClass("active");
          $listItem.find('> ul').toggleClass("show");
        });
        
      },

      unmatch : function() {
        $dropdown.off('click.mobileNav');
        // $nav_item.find(".js-back").remove();
      }
    });
  }



}
