'use strict';

import $ from 'jquery';
import 'jquery-ui-bundle';

export default class Rte {
  constructor() {
    $('.rte table').each(function() {
        let $this = $(this);

        if (!$this.parents('.table').length) {
            $this.wrap('<div class="table"></div>');
        }
    });

    // accordion for standard page
    $('.accordion-wrapper').accordion({
      collapsible: true,
      active: false
    });
  }
}
