'use strict';

import $ from 'jquery';
import 'slick-carousel';

export default class WhatsComing {
  constructor() {
    let whats_coming_slider = $('.whats-coming__slider');

    whats_coming_slider.slick({
      dots: true,
      arrows: true,
      prevArrow:"<span class='icon icon-circle-left slick-arrow slick-prev'></span>",
      nextArrow:"<span class='icon icon-circle-right slick-arrow slick-next'></span>"
    });
  }
}
