'use strict';

import $ from 'jquery';
import enquire from 'enquire.js';

export default class InstallationsSection {
  constructor() {

    let installations_section_header = $('.installations-section__header'),
      each_installation = $('.installation');

    enquire.register("screen and (max-width:1024px)", {

      match : function() {
        installations_section_header.removeAttr('data-parallax');
        each_installation.removeAttr('data-parallax');
      }
      
    });
  }
}
