'use strict';

import $ from 'jquery';
import '../../../_scripts/vendor/readmore.min';

export default class MediaListing {
  constructor() {
    let media_list = $('.media-listing__items'),
    each_item = $('.media-listing__link');

    const readmore_height = each_item.height() * 5;

    media_list.readmore({
      speed: 125,
      collapsedHeight: readmore_height,
      embedCSS: false,
      lessLink: '',
      moreLink: '<a href="#" class="media-listing__viewmore">VIEW MORE<span class="icon icon-chevron-small-down"></span></a>'
    });
  }
}
