'use strict';

import $ from 'jquery';
import 'jquery-ui-bundle';
import doT from 'dot';
import enquire from 'enquire.js';

export default class Map {
  constructor() {

    window.prototype = false;

    let filter_route = 'default',
    mapitems_endpoint = $('.map').data('mapitems'),
    mapdetails_endpoint = $('.map').data('mapdetails'),
    map_detailed = $('.map__detailed'),
    map_infowindow = $('.map__infowindow'),
    map_mobiletrigger = $('.map__mobiletrigger'),
    map_suggestedroutes = $('.map__suggestedroutes'),
    close_btn = $('.close', map_suggestedroutes),
    slidedown_infowindow = $('.icon-arrow_drop_down', map_infowindow),
    infowindow_open = false,
    true_center = {lat: 1.286261, lng: 103.845722},
    default_overlay,
    route_A_overlay,
    route_B_overlay,
    route_C_overlay,
    map_bounds = {
      north: 1.295765,
      south: 1.27745,
      east: 103.860870,
      west: 103.842834
    },
    active_map_filter = $('.map__filters-each.active'),
    filter_category = active_map_filter.data('show'),
    filter_category_id = active_map_filter.data('category-id');

    filter_category = filter_category.toLowerCase();


    let map_style = [
      {
        "featureType": "all",
        "elementType": "all",
        "stylers": [
          {
            "color": "#121728"
          }
        ]
      },
      {
        "featureType": "all",
        "elementType": "labels.text.fill",
        "stylers": [
          {
            "color": "#ffffff"
          }
        ]
      },
      {
        "featureType": "all",
        "elementType": "labels.text.stroke",
        "stylers": [
          {
            "color": "#000000"
          },
          {
            "lightness": 13
          }
        ]
      },
      {
        "featureType": "administrative",
        "elementType": "geometry.fill",
        "stylers": [
          {
            "color": "#000000"
          }
        ]
      },
      {
        "featureType": "administrative",
        "elementType": "geometry.stroke",
        "stylers": [
          {
            "color": "#144b53"
          },
          {
            "lightness": 14
          },
          {
            "weight": 1.4
          }
        ]
      },
      {
        "featureType": "landscape",
        "elementType": "all",
        "stylers": [
          {
            "color": "#08304b"
          }
        ]
      },
      {
        "featureType": "landscape.man_made",
        "elementType": "geometry.fill",
        "stylers": [
          {
            "color": "#1c243d"
          }
        ]
      },
      {
        "featureType": "landscape.man_made",
        "elementType": "geometry.stroke",
        "stylers": [
          {
            "color": "#1c243d"
          }
        ]
      },
      {
        "featureType": "landscape.natural",
        "elementType": "geometry",
        "stylers": [
          {
            "color": "#121728"
          }
        ]
      },
      {
        "featureType": "poi",
        "elementType": "all",
        "stylers": [
          {
            "visibility": "off"
          }
        ]
      },
      {
        "featureType": "poi",
        "elementType": "geometry",
        "stylers": [
          {
            "color": "#0c4152"
          },
          {
            "lightness": 5
          },
          {
            "visibility": "off"
          }
        ]
      },
      {
        "featureType": "poi",
        "elementType": "geometry.fill",
        "stylers": [
          {
            "visibility": "off"
          }
        ]
      },
      {
        "featureType": "poi.attraction",
        "elementType": "geometry",
        "stylers": [
          {
            "color": "#ff0000"
          }
        ]
      },
      {
        "featureType": "poi.park",
        "elementType": "geometry",
        "stylers": [
          {
            "visibility": "on"
          },
          {
            "color": "#79a96a"
          }
        ]
      },
      {
        "featureType": "poi.park",
        "elementType": "geometry.fill",
        "stylers": [
          {
            "visibility": "on"
          },
          {
            "weight": "1"
          }
        ]
      },
      {
        "featureType": "poi.park",
        "elementType": "geometry.stroke",
        "stylers": [
          {
            "visibility": "off"
          }
        ]
      },
      {
        "featureType": "poi.park",
        "elementType": "labels",
        "stylers": [
          {
            "visibility": "off"
          }
        ]
      },
      {
        "featureType": "road",
        "elementType": "geometry",
        "stylers": [
          {
            "color": "#ff0000"
          },
          {
            "visibility": "on"
          }
        ]
      },
      {
        "featureType": "road",
        "elementType": "geometry.fill",
        "stylers": [
          {
            "color": "#bfccf6"
          }
        ]
      },
      {
        "featureType": "road",
        "elementType": "geometry.stroke",
        "stylers": [
          {
            "color": "#05113d"
          }
        ]
      },
      {
        "featureType": "road.highway",
        "elementType": "geometry.fill",
        "stylers": [
          {
            "color": "#121728"
          }
        ]
      },
      {
        "featureType": "road.highway",
        "elementType": "geometry.stroke",
        "stylers": [
          {
            "color": "#121728"
          },
          {
            "lightness": 25
          }
        ]
      },
      {
        "featureType": "road.arterial",
        "elementType": "geometry.fill",
        "stylers": [
          {
            "color": "#121728"
          }
        ]
      },
      {
        "featureType": "road.arterial",
        "elementType": "geometry.stroke",
        "stylers": [
          {
            "color": "#121728"
          },
          {
            "lightness": 16
          }
        ]
      },
      {
        "featureType": "road.local",
        "elementType": "geometry",
        "stylers": [
          {
            "color": "#121728"
          }
        ]
      },
      {
        "featureType": "transit",
        "elementType": "all",
        "stylers": [
          {
            "color": "#121728"
          }
        ]
      },
      {
        "featureType": "transit.station.bus",
        "elementType": "all",
        "stylers": [
          {
            "visibility": "off"
          }
        ]
      },
      {
        "featureType": "transit.station.rail",
        "elementType": "labels.text",
        "stylers": [
          {
            "visibility": "off"
          }
        ]
      },
      {
        "featureType": "transit.station.rail",
        "elementType": "labels.text.fill",
        "stylers": [
          {
            "color": "#ffffff"
          }
        ]
      },
      {
        "featureType": "transit.station.rail",
        "elementType": "labels.text.stroke",
        "stylers": [
          {
            "color": "#000000"
          }
        ]
      },
      {
        "featureType": "water",
        "elementType": "all",
        "stylers": [
          {
            "color": "#74ccd2"
          }
        ]
      },
      {
        "featureType": "water",
        "elementType": "labels.text",
        "stylers": [
          {
            "visibility": "off"
          }
        ]
      },
      {
        "featureType": "water",
        "elementType": "labels.text.fill",
        "stylers": [
          {
            "color": "#000000"
          }
        ]
      },
      {
        "featureType": "water",
        "elementType": "labels.text.stroke",
        "stylers": [
          {
            "color": "#ffffff"
          }
        ]
      }
    ];

    let map = new google.maps.Map(document.getElementById('map'), {
      center: true_center,
      zoom: 16,
      styles: map_style,
      scrollwheel: false
    });

    $(window).load(function() {

      get_info_container(mapitems_endpoint, filter_category, filter_category_id, filter_route, 'mapinfo-receive');
      init_overlay();
      render_pins(mapitems_endpoint, filter_category, filter_category_id, filter_route);

      $('.map__filters-each').each(function(index, item) {
        if(index === 0) {
          $(item).trigger('click');
        }
      });

    });

    // handle mobile events
    enquire.register("screen and (max-width:1024px)", {
      // OPTIONAL
      // If supplied, triggered when a media query matches.
      match : function() {

        map_mobiletrigger.click(function() {
          map_suggestedroutes.toggleClass('show');
        });

        close_btn.click(function(event) {
          event.preventDefault();
          map_suggestedroutes.removeClass('show');
        });

        slidedown_infowindow.click(function(event) {
          event.preventDefault();

          if (infowindow_open) {
            map_infowindow.removeClass('show');
          } else {
            map_infowindow.addClass('show');
          }

          infowindow_open = !infowindow_open;
        });

        let mobile_back_btn = $('.map__detailed-mobile').find('.back');

        mobile_back_btn.click(function(event) {
          event.preventDefault();

          $('.map__detailed-mobile').removeClass('show');
          $('.map__infowindow').removeClass('preview');
        });
      }
    });

    // clicking on each installation
    $('body').on('click', '.mapinfo__each', function(event) {
      event.preventDefault();

      let self = $(this),
      installation_id = self.data('installationid'),
      self_lat = self.data('lat'),
      self_lng = self.data('lng'),
      self_latlng = new google.maps.LatLng(self_lat, self_lng);

      $('.marker').removeClass('glow');
      $(`.marker[data-installation_id=${installation_id}]`).addClass('glow');

      map.panTo(self_latlng);
      map.setZoom(16);

      if ($(window).width() > 960) {
        map_detailed.show('slide', { direction: 'right' }, 300);
        get_details(mapdetails_endpoint, installation_id);
      } else {
        $('.map__detailed-mobile').addClass('show');
        $('.map__infowindow').addClass('preview');

        get_detail_in_mobile(mapdetails_endpoint, installation_id);
      }
    });

    $('body').on('click', '.map__bg .back', function(event) {
      event.preventDefault();

      map_detailed.hide('slide', { direction: 'right' }, 300);
    });

    // categories toggling
    let filter_container = $('.map__filters'),
    category_btns = filter_container.find('.map__filters-each');

    category_btns.click(function(event) {
      event.preventDefault();

      category_btns.removeClass('active');
      $(this).addClass('active');

      let category = $(this).data('show');
      let category_id = $(this).data('category-id');

      filter_category = category.toLowerCase();
      filter_category_id = category_id;

      // 1) send class to filter container
      filter_container.removeClass().addClass('map__filters');
      filter_container.addClass(category.toLowerCase());

      // 2) hide all markers

      // and only show the selected routes
      // 3) show corresponding attractions
      get_info_container(mapitems_endpoint, filter_category, filter_category_id, filter_route, 'mapinfo-receive');


      render_pins(mapitems_endpoint, filter_category, filter_category_id, filter_route);
      // 4) handle panning and zoom levels
      $('.marker').removeClass('glow').addClass('hide');

      $(`.marker[data-category="${filter_category}"]`).removeClass('hide');

      let dest_latlng;

      if (category == 'hotel') {
        // do hotel stuff, panning
        let hotel_lat = $(this).data('lat'),
        hotel_lng = $(this).data('lng');

        dest_latlng = new google.maps.LatLng(hotel_lat, hotel_lng);
      } else {
        dest_latlng = new google.maps.LatLng(true_center.lat, true_center.lng);
      }

      map.panTo(dest_latlng);
      map.setZoom(16);
    });

    // suggested routes toggling
    let route_btns = $('.map__suggestedroutes').find('.route');

    route_btns.click(function(event) {

      event.preventDefault();

      if ($(this).hasClass('active')) {

        $(this).removeClass('active');

        filter_route = 'default';

        $('.marker').removeClass('hide');

      } else {

        route_btns.removeClass('active');
        $(this).addClass('active');

        let show = $(this).data('show');

        filter_route = show;

        $('.marker').each(function(idx, each) {

          let $each = $(each),
          routes = $each.data('route');

          routes = routes.split('|');

          if ($.inArray(filter_route, routes) > -1) {
            $each.removeClass('hide');
          } else {
            $each.addClass('hide');
          }

        });

      }

      render_overlay(filter_route);

      // filter
      get_info_container(mapitems_endpoint, filter_category, filter_category_id, filter_route, 'mapinfo-receive');
    });


    // helper functions
    function render_overlay(route) {
      // hide all
      default_overlay.setMap(null);
      route_A_overlay.setMap(null);
      route_B_overlay.setMap(null);
      route_C_overlay.setMap(null);


      switch(route) {
        case 'A':
        route_A_overlay.setMap(map);
        break;

        case 'B':
        route_B_overlay.setMap(map);
        break;

        case 'C':
        route_C_overlay.setMap(map);
        break;

        default:
        default_overlay.setMap(map);
      }
    }

    function init_overlay() {
      let map_overlay = new google.maps.GroundOverlay('/assets/ilight/images/map_layer.svg', map_bounds);
      map_overlay.setMap(map);

      default_overlay = new google.maps.GroundOverlay('/assets/ilight/images/Default.svg', map_bounds);
      default_overlay.setMap(map);

      route_A_overlay = new google.maps.GroundOverlay('/assets/ilight/images/Route1.svg', map_bounds);
      route_A_overlay.setMap(null);

      route_B_overlay = new google.maps.GroundOverlay('/assets/ilight/images/Route2.svg', map_bounds);
      route_B_overlay.setMap(null);

      route_C_overlay = new google.maps.GroundOverlay('/assets/ilight/images/Route3.svg', map_bounds);
      route_C_overlay.setMap(null);
    };

    function update_category(category) {
      filter_category = category;
    }

    function get_detail_in_mobile(endpoint, id) {
      let mapdetails_mobile_template = doT.template($('#mapdetail_mobile').html());

      $.ajax({
        url: endpoint,
        method: 'GET',
        dataType: 'json',
        data: {
          id: id
        },
        success: function(data) {
          let results = data,
          map_details_mobile_content = mapdetails_mobile_template(results);

          $('#mapdetail_mobile-receive').html(map_details_mobile_content);
        },
        error: function(error) {
          console.log(error);
        }
      });
    }

    function get_details(endpoint, id) {
      let mapdetails_template = doT.template($('#mapinfo__details').html());

      $.ajax({
        url: endpoint,
        method: 'GET',
        dataType: 'json',
        data: {
          id: id
        },
        success: function(data) {
          // render with dotjs
          let results = data,
          map_details_content = mapdetails_template(results);
          $('#mapdetails_receive').html(map_details_content);
        },
        error: function(error) {
          console.log(error);
        }
      });
    }

    function get_info_container(endpoint, category, category_id, route, container_id) {

      let mapinfo_template = doT.template($('#mapinfo-template').html());

      $.ajax({
        url: endpoint,
        method: 'GET',
        dataType: 'json',
        data: {
          category: category,
          category_id: category_id,
          route: route
        },
        success: function(data) {
          // render with dotjs
          for (let i=0; i < data.length; i++) {
            data[i].Category = data[i].Category.toLowerCase();
          }

          let results = data,

          map_info_content = mapinfo_template(results);

          $(`#${container_id}`).html(map_info_content);

          if (window.prototype) {
            $('.mapinfo__number .circle').removeClass().addClass('circle installations');
          }
        },
        error: function(error) {
          console.log(error);
        }
      });
    }

    function render_pins(endpoint, category, category_id, route) {

      $.ajax({
        url: endpoint,
        method: 'GET',
        dataType: 'json',
        data: {
          category: category,
          category_id: category_id,
          route: route
        },
        success: function(data) {
          let results = data,
          marker,
          i;

          // start putting the markers in
          for (i = 0; i < results.length; i++) {
            let lat = results[i].Lat,
            lng = results[i].Long,
            category = results[i].Category.toLowerCase(),
            route = results[i].Route,
            number = results[i].Number,
            installation_id = results[i].Installation_Id,
            type = results[i].Type;



            marker = new CustomMarker(
              new google.maps.LatLng(lat,lng),
              map,
              {
                category, route, number, installation_id, type, lat, lng
              }
            );


          }

        },
        error: function(error) {
          console.log(error);
        }
      });
    }

    function CustomMarker(latlng, map, args) {
      this.latlng = latlng;
      this.args = args;
      this.setMap(map);
    }

    CustomMarker.prototype = new google.maps.OverlayView();

    CustomMarker.prototype.draw = function() {

      let self = this;

      let div = this.div;

      let class_name = '';

      if (!div) {

        div = this.div = document.createElement('div');

        if (self.args.type == "marker") {
          class_name = 'marker';
        } else {
          class_name = 'route-marker';
        }

        // if numbers argument not provided
        if (typeof(self.args.number) !== 'undefined') {
          // update number inside custom marker
          div.innerText = self.args.number;
        }

        if (typeof(self.args.category) !== 'undefined') {
          // init category as data attribute for hiding and showing
          div.dataset.category = self.args.category;
          // add category as classname for styling
          class_name = `marker ${self.args.category}`;
        }

        div.className = class_name;

        if (typeof(self.args.route) !== 'undefined') {
          div.dataset.route = self.args.route;
        }

        if (typeof(self.args.lat) !== 'undefined') {
          div.dataset.lat = self.args.lat;
        }

        if (typeof(self.args.lng) !== 'undefined') {
          div.dataset.lng = self.args.lng;
        }

        if (typeof(self.args.route) !== 'undefined') {
          div.dataset.route = self.args.route;
        }

        if (typeof(self.args.installation_id) !== 'undefined') {
          div.dataset.installation_id = self.args.installation_id;
        }

        google.maps.event.addDomListener(div, "click", function(event) {
          google.maps.event.trigger(self, "click");

          let marker = $(event.target),
          installation_id = marker.data('installation_id'),
          marker_lat = marker.data('lat'),
          marker_lng = marker.data('lng'),
          marker_latlng = new google.maps.LatLng(marker_lat, marker_lng);

          get_details(mapdetails_endpoint, installation_id);

          map.panTo(marker_latlng);

          $('.marker').removeClass('glow');
          $(this).addClass('glow');

          if ($(window).width() > 960) {
            map_detailed.show('slide', { direction: 'right' }, 300);
            get_details(mapdetails_endpoint, installation_id);
          } else {
            get_detail_in_mobile(mapdetails_endpoint, installation_id);
            $('.map__detailed-mobile').addClass('show');
            map_infowindow.addClass('preview');
          }
        });

        var panes = this.getPanes();
        panes.overlayImage.appendChild(div);
      }

      var point = this.getProjection().fromLatLngToDivPixel(this.latlng);

      if (point) {
        div.style.left = (point.x - 10) + 'px';
        div.style.top = (point.y - 20) + 'px';
      }
    };

    CustomMarker.prototype.remove = function() {
      this.setMap(null);

      if (this.div) {
        this.div.parentNode.removeChild(this.div);
        this.div = null;
      }
    };

    CustomMarker.prototype.getPosition = function() {
      return this.latlng;
    };
  }
}
